.App-menu {
  background-color: #000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  padding-left: 40px;
  padding-right: 40px;
  height: 55px;
}

.App-menu-logo {
  color: #fefefe;
  font-size: 48px;
  margin: 5px;
}

.App-menu-nav {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-menu-nav-link {
  color: #fefefe;
  text-decoration: none;
  font-size: 18px;
  margin: 10px;
  transition: .5s;
}

.App-menu-nav-link:hover {
  color: #e0e0e0;
}

.home-greetings {
  background-position: 50% 30%; 
  background-size: cover;
  width: 100%;
  height: 500px;
}

.home-greetings-bg {
  margin: 0;
  width: 100%;
  height: 100%;
  background: #00000098;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.home-greetings-text {
  font-size: 72px;
  color: white;
  margin: 10px;
  text-align: center;
}

.home-greetings-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.home-greetings-button{
  width: 100px;
  height: 10px;
  background: #fefefe;
  color: black;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 18px;
  margin: 10px;
  text-decoration: none;
  transition: .5s;
}

.home-greetings-button:hover {
  background: #e0e0e0;
}

.home-greetings-content {
  margin-bottom: 1.3%;
}

.home-greetings-bg .slick-slider {
  width: 100%;
  overflow: hidden;
  position: absolute;
  z-index: -1;
  height: 500px;
}

.home-slider-image {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.home-slider-image img {
  width: 100%;
  overflow: hidden;
  max-height: 500px;
  object-fit: cover;
  object-position: 50% 40%;
}

.home-greetings-bg .slick-slide {
  display: inline;
  float: left;
}

.about-me {
  background: #fefefe;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.about-me-heading {
  color: black;
  font-size: 36px;
  text-align: center;
}

.about-me-bubble {
  background: rgb(250, 250, 250);
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75%;
  padding: 20px;
  border-radius: 5px;
  margin: 20px;
}

.about-me-avatar {
  width: 50%;
  border-radius: 500px;
}

.about-me-bubble-text {
  font-size: 24px;
}

.about-me-gallery {
  display: grid;
  justify-content: center;
  align-items: center;
  width: 90%;
  flex-wrap: wrap;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  margin: 20px;
}

#home-rep-gal .about-me-gallery-item:nth-child(1){
    grid-column: 1/3;
}   

#home-rep-gal .about-me-gallery-item:nth-child(5){
  grid-row: 2;
  grid-column: 2/4;
}   

.about-me-gallery-item {
      display: grid;
  width: 100%;
    background-size: cover;
    background-position: center;
    text-align: center;
    align-content: center;
}

.about-me-gallery-item img {
  width: 100%;
  border-radius: 5px;
  height: 500px;
  object-fit: cover;
}

.portfolio-item-img{
  width: 95%;
  margin: 10px;
}

.portfolio-gallery-item{
  width: 50%;
}

.portfolio-gallery {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.slick-prev,
.slick-next{
  position: fixed!important;
  z-index:999!important;
  transition: 5s!important;
}

.slick-prev {
  top: 50%!important;
  left: 5%!important;
}

.slick-next {
  top: 50%!important;
  right: 5%!important;
}


.slick-prev::before {
  color: black!important;
}

.slick-next::before {
  color: black!important;
}

#SliderGal .slick-slide {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}

.portfolio-gallery-heading {
  font-size: 30px;
  text-align: center;
}

.portfolio-heading {
  font-size: 72px;
  text-align: center;
}

.contacts-heading {
  font-size: 72px;
  text-align: center;
}

.logo-link {
  text-decoration: none;
}

#home-art-gal .about-me-gallery-item:nth-child(3) {
  grid-row: 1;
  grid-column: 3/5;
}
#home-art-gal .about-me-gallery-item:nth-child(4) {
  grid-row: 2;
  grid-column: 1/3;
}

.contacts-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px;
  width: 200px;
}

.contact-link {
  font-size: 32px;
  margin-right: auto;
  margin-left: 10px;
}


.contact-icon {
  font-size: 40px;
  margin: 10px;
}

.contact-a {
  color: black;
  text-decoration: none;
  transition: .5s;
}

.contact-a:hover {
  color: rgb(51, 51, 51);
}

@media (max-width:768px){

  .home-slider-image img {
    width: auto;
  }
}

@media (max-width:750px){
  .App-menu {
    height: 1%;
    flex-direction: column;
  }

  .App-menu-nav {
    margin: 20px;
  }

  .logo-link h1 {
    font-size: 52px;
  }

  .App-menu-nav-link {
    font-size: 18px;
  }

  .home-greetings-text {
    font-size: 60px;
  }

  .about-me-bubble {
    flex-direction: column;
  }

  .about-me-gallery {
    display: flex;
  }
  
  .portfolio-gallery-heading {
    width: 75%;
    margin-left: auto;
    margin-right: auto;
  }

  .portfolio-gallery-item {
    width: 100%;
  }

  .portfolio-heading {
    font-size: 60px;
  }

  .contacts-heading {
    font-size: 60px;
  }

  .slick-prev {
    left: 4%!important;
  }

  .slick-next {
    right: 4%!important;
  }
}

@media (max-width:400px){
  .logo-link h1 {
    font-size: 44px;
  }
  .home-greetings-text {
    font-size: 50px;
  }

  .portfolio-heading {
    font-size: 55px;
  }
  .contacts-heading {
    font-size: 55px;    
  }
}